	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝の「ドキュメンタリーとフィクション」を読むことは、著者の経験や自分の経験の発展のような小説です。私はまだ読書を終わらせていない、と私は今結論することはできません。 1949年以降の中国社会におけるユニークな文化現象、あるいは都市社会生活の中でユニークなことについて語っています。人民解放軍が市に入った後、社会には2つの異なる文化が形成されました.1つは延安が鋳造した革命文化であり、小説には「仲間」文化として、もうひとつは元々は街にあった文化でした。 \"小さな市民\"の文化。</p>\n<p>この小説はこの矛盾やギャップを使ってプロットを描く。しかし、これらの2つの文化は近代都市にしばらく存在し、人々の社会生活、政治的生活、心理的構造に不可避的な影響を及ぼしてきた。現代中国社会の社会発展を研究することは、この点を無視することはできない。最近では、市場経済とコモディティ経済の発展により、文化の差が小さくなったり小さくなったりしています。商品文化は大きな力を持ち、すべての文化的障壁を打破することができます。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0025.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0025.md","frontmatter":{"title":"2月5日 土曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0025.md absPath of file >>> MarkdownRemark"}}

